import { sanityClient } from "@/lib/sanity";
import BlogPostClient from "./BlogPostClient";

export async function generateMetadata({ params }) {
  const slug = params?.slug;

  if (!slug) {
    return { title: "Blog | Cloud101 Academy" };
  }

  const post = await sanityClient.fetch(
    `*[_type=="post" && slug.current==$slug][0]{
      title,
      excerpt,
      mainImage{asset->{url}}
    }`,
    { slug }
  );

  if (!post) {
    return { title: "Blog | Cloud101 Academy" };
  }

  return {
    title: `${post.title} | Cloud101 Academy`,
    description: post.excerpt,
    alternates: {
      canonical: `https://cloud101.in/blog/${slug}`,
    },
    openGraph: {
      title: post.title,
      description: post.excerpt,
      type: "article",
      images: post.mainImage?.asset?.url
        ? [{ url: post.mainImage.asset.url }]
        : [],
    },
  };
}

export default async function BlogPostPage({ params }) {
  const slug = params.slug; // ðŸ”‘ no optional chaining

  const post = await sanityClient.fetch(
    `*[_type=="post" && slug.current==$slug][0]{
      _id,
      title,
      excerpt,
      publishedAt,
      mainImage{asset->{url}},
      categories[]->{_id,title},
      author->{name,image{asset->{url}}},
      content
    }`,
    { slug }
  );

  if (!post) return null;

  const relatedPosts = post.categories?.length
    ? await sanityClient.fetch(
        `*[_type=="post" &&
          references($categoryIds) &&
          slug.current != $slug][0...3]{
            _id,
            title,
            "slug": slug.current,
            excerpt,
            mainImage{asset->{url}},
            author->{name,image{asset->{url}}}
          }`,
        {
          categoryIds: post.categories.map((c) => c._id),
          slug,
        }
      )
    : [];

  return <BlogPostClient post={post} relatedPosts={relatedPosts} />;
}
